<template>
<section class="subpage--main">
  <Card>
    <div class="condition">
      <label class="condition__item">
        操作人：
        <Input
          class="input"
          v-model="table.unsyncQuery.userName"
          placeholder="请输入操作人"
        />
      </label>
      <label class="condition__item">
        操作IP：
        <Input
          class="input"
          v-model="table.unsyncQuery.requestIp"
          placeholder="请输入操作IP"
        />
      </label>
      <Button
        class="condition__submit"
        type="info"
        shape="circle"
        icon="ios-search"
        @click="table.apply"
      >查询</Button>
    </div>
    <EasyTable
      :table="table"
      stripe
    />
  </Card>
</section>
</template>

<script>
import EasyTable, { createTable } from '@/components/EasyTable'

export default {
  components: {
    EasyTable,
  },
  data() {
    return {
      table: createTable({
        query: {
          userName: null,
          requestIp: null
        },
        loadData: async ({
          PAGE_SIZE,
          current,
          query,
        }) => {
          const {
            list: items,
            total
          } = await this.$api.system.listLoginLogs.send({
            'page.pageSize': PAGE_SIZE,
            'page.pageNum': current,
            ...query
          })
          return {
            items,
            total
          }
        },
        COLUMNS: [
          {
            type: 'index',
            maxWidth: 100,
            align: 'center',
            title: '序号'
          },
          {
            title: '操作人',
            key: 'account',
            align: 'center'
          },
          {
            title: '登录状态',
            key: 'successValue',
            align: 'center'
          },
          {
            title: '操作时间',
            key: 'logDt',
            align: 'center'
          },
          {
            title: '操作IP',
            key: 'requestIp',
            align: 'center'
          }
        ],
      })
    }
  },
  created() {
    this.table.apply()
  }
};
</script>
